<template>
   <div :id="id" :style="{height:chartHeight,width:chartWidth}" class="seriesBar"></div>
</template>

<script>
import echarts from 'echarts'
export default {
 name: 'SeriesBar',
 props:{
     id:{
         type:String,
         default:'seriesBar'
     },
     chartHeight:{
         type:String,
         default:'300px'
     },
     chartWidth:{
         type:String,
         default:'100%'
     },
     seriesBarData:{
         type:Object,
         default(){
             return {}
         }
     },
     color:{
         type:Array,
         default(){
             return ['#03B187', '#5AB3FF', '#D5CFFF']
         }
     },
      gradual:{ //渐变色
         type:Array,
         default(){
             return [
                {
                    color:'#8EE9CA',
                    gradual:'#03B187'
                },
                {
                    color:'#B3DCFF',
                    gradual:'#5AB3FF'
                },
                {
                    color:'#ECEAFF',
                    gradual:'#D5CFFF'
                },
                {
                    color:'#ECEAFF',
                    gradual:'#D5CFFF'
                }
            ]
         }
     },
     labelShow:{
         type:Boolean,
         default:false
     },
     isMax:{
         type:Boolean,
         default:false
     },
     isMin:{
         type:Boolean,
         default:false
     }
 },
 data() {
 return {
     chart:null,
     labelOption:{
         normal: {
            show: false, //柱状上显示数据
            position: 'insideBottom',
            distance: 15,
            align: 'left',
            verticalAlign: 'middle',
            rotate: 90,
            formatter: '{c}',
            fontSize: 12,
            rich: {
                name: {
                    textBorderColor: '#fff'
                }
            }
        }
     }
 }
 },
 created(){},
 mounted(){
     this.initChart();
 },
 watch:{
     seriesBarData(curVal,oldVal){
         this.initChart(); 
     }
 },
 methods:{
     initChart(){
        const self=this;
        self.chart = echarts.init(document.getElementById(self.id));
        self.chart.setOption(self.getSeriesBarOption());
        window.addEventListener('resize', function(){
            if(self.chart){
                self.chart.resize()
            }
        })
     },
     getSeriesBarOption(){
         const self=this
         self.labelOption.normal.show=self.labelShow
         return {
            color: self.color, //柱体颜色
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'shadow',
                    shadowStyle:{
                        color:'rgba(142,233,202,0.2)' // 鼠标移入背景灰色的颜色改变 
                    }
                },
            },
            title: {
                text: self.seriesBarData.title || [],
                textStyle: {
                    color: '#696969',
                    fontSize: 14,
                }
            },
            legend: {
                right: 21,
                top: 0,
                data:self.seriesBarData.legendData || [],
                textStyle:{
                    color: self.color,
                }
            },
            grid: {
                left: "0.5%",
                right: "0.5%",
                bottom: "10%",
                containLabel: true
            },
            // toolbox: {
            // 　show: true,
            // 　　feature: {
            // 　　　　saveAsImage: {
            // 　　　　show:true,
            // 　　　　excludeComponents :['toolbox'],
            // 　　　　pixelRatio: 2      
            // 　　　　}
            // 　　},
            //   right: '50px'
            // },
            calculable: true,
            xAxis: [
                {
                    type: 'category',
                    axisLine:{
                        show:false,
                        lineStyle: {
                          color: '#aaa'
                        }
                    },
                    axisTick:{
                       show:false
                    },
                    data:self.seriesBarData.xAxisData || [], //X坐标数据 
                    // 坐标轴刻度标签的相关设置  
                    axisLabel: {  
                        interval:0,  
                        rotate:   self.seriesBarData.xAxisData.length > 8 ? 45 : 0 //代表逆时针旋转45度
                    }  
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    axisLine:{
                        show:false,
                        lineStyle: {
                          color: '#aaa'
                        }
                    },
                    axisTick:{
                       show:false
                    },
                    min: !self.isMax ? undefined : function(value) {
                        return 0
                    },
                    max: !self.isMax ? undefined : function(value) {
                        return  1 
                    }
                }
            ],
            series: self.seriesInitFn()
         }
     },
     seriesInitFn(){
       const _self=this  
        //随机颜色
        function getRandomColor (){
            const random=parseInt(Math.random()*Math.pow(32,4));
                const v=('00000'+random.toString(16)).substr(-4);
                return '#'+ v
        }
       const seriesData= JSON.parse(JSON.stringify(_self.seriesBarData.seriesData || [])) 
       const obj={
            type: 'bar',
            barWidth: 30,
            label: _self.labelOption
        }

     let gradualArr=_self.gradual
     if(_self.gradual.length < seriesData.length){
         let num=seriesData.length - _self.gradual.length
         for(let i=0; i<num; i++){
                gradualArr.push({
                    color:getRandomColor(),
                    gradual:getRandomColor()
                })
         }
     }
     seriesData.forEach((item, index) =>{
         //柱子间距统一去掉
         item.barGap='0'
         //渐变设置
         item.itemStyle= {
                normal: {
                    barBorderRadius: [5,5,0,0],
                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: gradualArr[index].gradual // 0% 处的颜色
                    }, {
                        offset: 1,
                        color: gradualArr[index].color // 100% 处的颜色
                    }], false)
                },
            }
         Object.assign(item, obj)
      })

     return seriesData
     }

 },
 beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose();
      this.chart = null;
    }
}
</script>

<style lang='scss' scoped>
</style>



