<template>
    <div class="screeing">
        <el-dialog title="筛选样本学生"  :visible.sync="dialogFormVisible" :before-close="haddTheTestClose">
            <el-form :model="form">
                <el-form-item label="已达成学生" >
                    <template>
                        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                        <div style="margin: 15px 0;"></div>
                        <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange" style="margin-left: 82px;">
                            <el-checkbox v-for="city in citiesarr" :label="city.val" :key="city.val">{{city.val}}</el-checkbox>
                        </el-checkbox-group>
                    </template>
                </el-form-item>
                <el-form-item label="未达成学生" >
                    <template>
                        <el-checkbox :indeterminate="isIndeterminate1" v-model="checkAll1" @change="handleCheckAllChange1">全选</el-checkbox>
                        <div style="margin: 15px 0;"></div>
                        <el-checkbox-group v-model="checkedCities1" @change="handleCheckedCitiesChange1" style="margin-left: 82px;">
                            <el-checkbox v-for="city in citiesmin" :label="city.val" :key="city.val">{{city.val}}</el-checkbox>
                        </el-checkbox-group>
                    </template>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="setCourseCancel('form')">取 消</el-button>
                <el-button type="primary" @click="setCourseCreate('form')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
    props: {
        value: {
            type: Boolean,
            default: false
        },
        courseData: {
            type: Array,
            default: []
        },
        indexVal:{
            type: Number,
            default: ''
        }
    },
    data(){
        return{
            checkAll: false,
            checkAll1: false,
            checkedCities: [],
            checkedCities1: [],
            cities: [],
            citiesarr:[],
            citiesmin:[],
            isIndeterminate: true,
            isIndeterminate1: true,
            dialogFormVisible: false,
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
        }
    },
    watch: {
        value(curVal) {
            if (curVal) {
                this.resetTemps(this.courseData,this.indexVal);
            }
            this.dialogFormVisible = curVal;
        }
    },
    methods: {
        handleCheckAllChange(val) {
            let arr = []
            this.citiesarr.forEach(vo=>{
                arr.push(vo.val)
            })
            this.checkedCities = val ? arr : [];
            this.isIndeterminate = false;
        },
        handleCheckAllChange1(val) {
            let arr = []
            this.citiesmin.forEach(vo=>{
                arr.push(vo.val)
            })
            this.checkedCities1 = val ? arr : [];
            this.isIndeterminate1 = false;
        },
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.citiesarr.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.citiesarr.length;
        },
        handleCheckedCitiesChange1(value) {
            let checkedCount = value.length;
            this.checkAll1 = checkedCount === this.citiesmin.length;
            this.isIndeterminate1 = checkedCount > 0 && checkedCount < this.citiesmin.length;
        },
        setCourseCancel(formName) {
            this.dialogFormVisible = false
            this.$emit("close", false)
        },
        setCourseCreate(formName) {
            var addcheckedCities = this.checkedCities.concat(this.checkedCities1);
            let arr = []
            this.courseData[this.indexVal].newstudentScore.forEach(vo => {
                addcheckedCities.forEach(co=>{
                    if(co === vo[0]){
                        arr.push([vo[0],vo[1]])
                    }
                })
            });
            this.courseData[this.indexVal].studentScore = arr
            const formData = JSON.parse(JSON.stringify(this.courseData))
            this.$emit("success", formData)
            this.setCourseCancel("form")
        },
        haddTheTestClose() {
            this.dialogFormVisible = false
            this.$emit("close", false)
        },
        resetTemps(row,index){
            let arr = []
            let arrmin = []
            let checkedCitiesone = []
            let checkedCitiestwo = []
            row.forEach((item,i) => {
                if(i === index){
                    item.newstudentScore.forEach(vo => {
                        if(Number(item.target) <= Number( vo[1])){
                            arr.push({
                                id:Number(vo[1]),
                                val:vo[0]
                            })
                        }else {
                            arrmin.push({
                                id:Number(vo[1]),
                                val:vo[0]
                            })
                        }
                    });
                    item.studentScore.forEach(vo => {
                        if(Number(item.target) <= Number( vo[1])){
                            checkedCitiesone.push(vo[0])
                        }else {
                            checkedCitiestwo.push(vo[0])
                        }
                    });
                   
                }
            });
            this.checkedCities = checkedCitiesone
            this.checkedCities1 = checkedCitiestwo
            this.citiesarr = arr
            this.citiesmin = arrmin
            if(this.checkedCities.length && this.checkedCities.length === this.citiesarr.length){
                this.isIndeterminate = false
                this.checkAll = true
            }else if (this.checkedCities.length && this.checkedCities.length < this.citiesarr.length) {
                this.isIndeterminate = true
                this.checkAll = false
            }else {
                this.isIndeterminate = false
                this.checkAll = false
            }
            if(this.checkedCities1.length && this.checkedCities1.length === this.citiesmin.length){
                this.isIndeterminate1 = false
                this.checkAll1 = true
            }else if (this.checkedCities1.length && this.checkedCities1.length < this.citiesmin.length) {
                this.isIndeterminate1 = true
                this.checkAll1 = false
            }else {
                this.isIndeterminate1 = false
                this.checkAll1 = false
            }
        },
    }
}
</script>
<style lang='scss' scoped>
</style>


