<template>
<div class="set-course ComparisonYears">
    <el-dialog title="历年数据对比" :visible.sync="dialogFormVisible" :before-close="haddTheTestClose" class="set-el-dialog set-course-dialog" width="80%">
        <div class="search-input-suffix gap" style="display: inline-block;position: absolute;left: 140px;top: 17px;">
            <el-select v-model="form.year" placeholder="请选择年级" @change="courseIdSelect">
                <el-option v-for="item in yearlist" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
            </el-select>
        </div>
        <div class="ComparisonYears_dv"  v-for="(item, indexq) in ComparisonYearslist" :key="indexq">
            <p class="ComparisonYears_p"> 
                <span class="ComparisonYears_spanone"></span>
                <span class="ComparisonYears_sapntwo">{{item.examine}}</span>
            </p>
            <div class="teacherResults_container_content">
                <!-- <p class="zanwu" v-if="courseAnalysis.length === 0">暂无数据</p> -->
                <div class="graph" style="margin-top: 15px;display: flex;">
                    <table cellspacing="0"  cellpadding="0"  border="0" class="el-table el-table--border">
                        <colgroup>
                            <col/>
                            <col/>
                            <col  v-for="(res, index) in item.tableHeader" :key="index">
                        </colgroup>
                        <tr>
                            <th style="width:100px;text-align:center;" v-for="(items, index) in item.tableHeader" :key="index">{{items.goalName}}</th>
                        </tr>
                        <tr v-for="(iteme, indexs) in item.table" :key="indexs">
                            <td  v-for="(item, index) in iteme.relations" :key="index" >
                                {{item.ke}}
                            </td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="ComparisonYears_echarts" v-if="indexq == 0">
                <echarts7 id="sbilityQueryBar" :series-bar-data="seriesBarData" :isMax="true" :isMin="true"></echarts7>
            </div>
        </div>
    </el-dialog>
</div>
</template>

<script>
import echarts7 from "@/components/echaars/echars7"
export default {
    props: {
        value: {
            type: Boolean,
            default: false
        },
    },
    components: {echarts7},
    data() {
        return {
            seriesBarData: {},
            dialogFormVisible: false,
            form:{
                year:''
            },
            yearlist:[
                {
                    id:'1',
                    name:'2014-2015学年'
                },
                {
                    id:'2',
                    name:'2015-2016学年'
                },
                {
                    id:'3',
                    name:'2016-2017学年'
                },
            ],
            ComparisonYearslist:[
                {   
                    examine: "总览：《汽车试验与测试技术》课程历年达成情况",
                    examineId: "628280854518431744",
                    success: 200,
                    table:[
                        {
                           examName:'2015-2016学年',
                            relations:[
                                {
                                    goalId: "0",
                                    goalName: "学年",
                                    ke:'2015-2016学年'
                                },
                                {
                                    goalId: "1",
                                    goalName: "课程目标1",
                                     ke:0.7
                                },
                                {
                                    goalId: "2",
                                    goalName: "课程目标2",
                                     ke:0.7
                                },
                                {
                                    goalId: "3",
                                    goalName: "课程目标3",
                                     ke:0.7
                                },
                                {
                                    goalId: "4",
                                    goalName: "课程目标4",
                                     ke:0.7
                                },
                            ]
                        },
                        {
                           examName:'2015-2016学年',
                            relations:[
                                {
                                    goalId: "0",
                                    goalName: "学年",
                                    ke:'2015-2016学年'
                                },
                                {
                                    goalId: "1",
                                    goalName: "课程目标1",
                                     ke:0.7
                                },
                                {
                                    goalId: "2",
                                    goalName: "课程目标2",
                                     ke:0.7
                                },
                                {
                                    goalId: "3",
                                    goalName: "课程目标3",
                                     ke:0.7
                                },
                                {
                                    goalId: "4",
                                    goalName: "课程目标4",
                                     ke:0.7
                                },
                            ]
                        },
                        {
                           examName:'2015-2016学年',
                            relations:[
                                {
                                    goalId: "0",
                                    goalName: "学年",
                                    ke:'2015-2016学年'
                                },
                                {
                                    goalId: "1",
                                    goalName: "课程目标1",
                                     ke:0.7
                                },
                                {
                                    goalId: "2",
                                    goalName: "课程目标2",
                                     ke:0.7
                                },
                                {
                                    goalId: "3",
                                    goalName: "课程目标3",
                                     ke:0.7
                                },
                                {
                                    goalId: "4",
                                    goalName: "课程目标4",
                                     ke:0.7
                                },
                            ]
                        },
                    ],
                },
                {   
                    examine: "总览：《汽车试验与测试技术》课程历年达成情况",
                    examineId: "628280854518431744",
                    success: 200,
                    table:[
                        {
                           examName:'2015-2016学年',
                            relations:[
                                {
                                    goalId: "0",
                                    goalName: "学年",
                                    ke:'2015-2016学年'
                                },
                                {
                                    goalId: "1",
                                    goalName: "总分",
                                     ke:0.7
                                },
                                {
                                    goalId: "2",
                                    goalName: "平均分",
                                     ke:0.7
                                },
                                {
                                    goalId: "3",
                                    goalName: "达成度",
                                     ke:0.7
                                },
                                {
                                    goalId: "4",
                                    goalName: "达成比例",
                                     ke:0.7
                                },
                            ]
                        },
                        {
                           examName:'2015-2016学年',
                            relations:[
                                {
                                    goalId: "0",
                                    goalName: "学年",
                                    ke:'2015-2016学年'
                                },
                                {
                                    goalId: "1",
                                    goalName: "课程目标1",
                                     ke:0.7
                                },
                                {
                                    goalId: "2",
                                    goalName: "课程目标2",
                                     ke:0.7
                                },
                                {
                                    goalId: "3",
                                    goalName: "课程目标3",
                                     ke:0.7
                                },
                                {
                                    goalId: "4",
                                    goalName: "课程目标4",
                                     ke:0.7
                                },
                            ]
                        },
                        {
                           examName:'2015-2016学年',
                            relations:[
                                {
                                    goalId: "0",
                                    goalName: "学年",
                                    ke:'2015-2016学年'
                                },
                                {
                                    goalId: "1",
                                    goalName: "课程目标1",
                                     ke:0.7
                                },
                                {
                                    goalId: "2",
                                    goalName: "课程目标2",
                                     ke:0.7
                                },
                                {
                                    goalId: "3",
                                    goalName: "课程目标3",
                                     ke:0.7
                                },
                                {
                                    goalId: "4",
                                    goalName: "课程目标4",
                                     ke:0.7
                                },
                            ]
                        },
                    ]
                },
            ],
        };
    },
    created() {},
    mounted() {
        this.ComparisonYearslist.forEach(item=>{
            item.tableHeader=JSON.parse(JSON.stringify(item.table[0].relations))
            // item.table.splice(0,1)
        })
         this.getecharts()
    },
    watch: {
        value(curVal) {
            this.dialogFormVisible = curVal;
           
        }
    },
    methods: {
        haddTheTestClose() {
            this.dialogFormVisible = false
            this.$emit("close", false)
        },
        courseIdSelect(){

        },
        getecharts(){
            let goalratioMark = [
                {
                    legendData : '历年达成情况对比',
                    name: "课程目标 1",
                    point: "指标点1-3",
                    marks:[
                        {
                            goalmark: 0.4,
                            grade: 2016,
                        },
                        {
                            goalmark: 0.5,
                            grade: 2017,
                        },
                        {
                            goalmark: 0.61718372,
                            grade: 2018,
                        },
                    ]
                },
                {
                    legendData : '历年达成情况对比',
                    name: "课程目标 2",
                    point: "指标点1-3",
                    marks:[
                        {
                            goalmark: 0.6,
                            grade: 2016,
                        },
                        {
                            goalmark: 0.4,
                            grade: 2017,
                        },
                        {
                            goalmark: 0.2,
                            grade: 2018,
                        },
                    ]
                },
                {
                    legendData : '历年达成情况对比',
                    name: "课程目标 3",
                    point: "指标点1-3",
                    marks:[
                        {
                            goalmark: 0.61718372,
                            grade: 2016,
                        },
                        {
                            goalmark: 0.7,
                            grade: 2017,
                        },
                        {
                            goalmark: 0.8,
                            grade: 2018,
                        },
                    ]
                },
            ]
            // this.seriesBarData=data
            if(goalratioMark){
                // this.pointListsifon = res.data.goalratioMark
                let arr = []
                let arr1 =[]
                let arr2 = []
               goalratioMark.forEach((item, index) => {
                    arr1.push(item.name)
                    if(index === 0) {
                    item.marks.forEach((item1, index1) => {
                        arr2.push(String(item1.grade) + '-'+ Number(item1.grade + 1)+ '学年')
                        arr.push({
                        grade: item1.grade,
                        children: [item1.goalmark]
                        })
                    })
                    } else {
                    item.marks.forEach((item1, index1) => {
                        arr.forEach((item2, index2) => {
                        if(item1.grade === item2.grade) {
                            item2.children.push(item1.goalmark)
                        }
                        })
                    })
                    }
                })
                const top = arr2;
                const tip = arr1
                const echartData = {
                    title:'历年达成情况对比',
                    legendData: top,
                    xAxisData: tip,
                    seriesData: top.map(item => {
                        return {
                            name: item,
                            barGap: "0", //柱状图间距
                            data: []
                        };
                    })
                };
                arr.forEach((ele,index) => {
                    echartData.seriesData[index].data = ele.children
                });
                this.seriesBarData = echartData;
            }
        }
    }
};
</script>

<style lang="scss">
    .ComparisonYears{
        .el-dialog__title{
            font-size:18px;
            font-family:PingFang SC;
            font-weight:bold;
            color:rgba(50,50,50,1);
        }
        .el-input__inner{
            width:142px;
            height:32px;
            border:1px solid rgba(3,177,135,1);
            border-radius:5px;
            
        }
        .el-input__icon{
                line-height: 32px;
            }
        .ComparisonYears_dv{
            .ComparisonYears_echarts{
                margin-top: 20px;
            }
            .ComparisonYears_p{
                .ComparisonYears_spanone{
                    width:5px;
                    height:14px;
                    background:rgba(3,177,135,1);
                    display: inline-block;
                    float: left;
                    margin-top: 3px;
                    margin-right: 7px;
                }
                .ComparisonYears_sapntwo{
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:bold;
                    color:rgba(50,50,50,1);
                }
            }
            .teacherResults_container_content {
                .small-title {
                    font-size: 16px;
                    color: #323232;
                    font-weight: bold;
                }

                .zanwu {
                    text-align: center;
                    margin-top: 20px;
                    letter-spacing: 8px;
                }

                .graph {
                    overflow-x: auto;

                    .graph_dv1 {
                        text-align: center;
                        height: 100px;
                        line-height: 100px;
                        width: 100px;
                        background-color: #f8f8f8;
                        margin-right: 15px;
                        border-radius: 4px;
                        position: relative;

                        .graph_dvp {
                            width: 20px;
                            margin: 0 auto;
                            line-height: 24px;
                            color: #03b187;
                            font-weight: bold;
                            font-size: 18px;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                        }
                    }

                    .sign-name {
                        line-height: 40px;
                        text-align: center;
                        color: #333;
                        font-size: 14px;
                    }

                    .el-table--border {
                        td {
                            border-bottom: 1px solid #EBEEF5;
                            text-align: center;
                        }

                        th {
                            background: rgba(248, 248, 248, 1);
                        }
                    }
                }

                ul {
                    width: 100%;
                    margin: 10px 0;

                    li {
                        height: 38px;
                        width: 33%;
                        float: left;
                        font-size: 14px;
                        line-height: 38px;
                        list-style-type: none;
                        text-align: left;

                        b {
                            font-weight: normal;
                            color: #323232;
                            margin-right: 5px;
                        }

                        span {
                            color: #323232;
                        }
                    }
                }

                .hspace {
                    padding: 20px 0;
                }
            }
        }
    }
</style>




